'use strict';

import Base from './base.js';
import Create from './sitemap.js';
function pad(value) {
    if(parseInt(value) < 10) {
        return '0' + value;
    } else {
        return value;
    }
}
function getAttrFromString(str, node, attr) {
    var regex = new RegExp('<' + node + ' .*?' + attr + '="(.*?)"', "gi"), result, res = [];
    while ((result = regex.exec(str))) {
        res.push(result[1]);
    }
    return res;
}
