  // 首页
  async categoriesAction()
  {
    let items=await this.model("item").order('id DESC').select();
    var content="";
    var urls=[];
    if(!think.isEmpty(items))
    {
    for (var item of items) {
      var url="/"+item.urlrewrite+"-"+item.id;
      var lM=new Date(item.lastmodified);
      var lastM=lM.getFullYear() + "-" + ('0' + (lM.getMonth()+1)).slice(-2) + "-" + ('0' + lM.getDate()).slice(-2) + "T" + pad(lM.getHours()) + ":" + pad(lM.getMinutes()) + ":" + pad(lM.getSeconds())+"+07:00";
      var obj={url: url,lastmodISO:lastM,changefreq: 'weekly', priority: 0.8};
      urls.push(obj);
    }
    var obj={url: "/video",lastmodISO:"2017-11-20T12:26:38+07:00",changefreq: 'weekly', priority: 0.8};
    urls.push(obj);
    var sm = require('sitemap');
    content = sm.createSitemap({
       hostname: 'http://www.tintech.vn',
       cacheTime: 600000,  //600 sec (10 min) cache purge period
       urls:urls
     }).toString();
   }
   this.header('Content-Type', 'application/xml');
   this.end(content)
  }
