  async articleAction()
  {
   var aurl=await this.get("aurl");
   var ct=aurl.substring(0, 4)+"-"+aurl.substring(4, 6)+"-"+aurl.substring(6, 8);
   var items=await this.model("article").where({createtime:['like', '%'+ct+'%'],ispublished: 1}).order('lastmodified DESC').select();;
   var content="";
   if(!think.isEmpty(items))
   {
     var urls=[];
     var imgs=[];
     var videos=[];
     for (var item of items) {
       var url="/"+item.itemurlrewrite+"-"+item.item+"/"+item.urlrewrite+"-"+item.id;

      var vds=getAttrFromString(item.content, 'video', 'src');
       var images=getAttrFromString(item.content, 'img', 'src');
       var alts=getAttrFromString(item.content, 'img', 'alt');
       for (var i=0;i<images.length;i++) {
         var ig={
            url: 'http://tintech.vn'+images[i],
            caption: alts[i],
            title: item.title,
            geoLocation: 'Ho Chi Minh, Vietnam',
            license: 'http://tintech.vn/'
          };
          imgs.push(ig);
       }
      //  for (var it of vds) {
      //    var vd={thumbnail_loc: 'http://test.com/tmbn1.jpg', title: 'A video title', description: 'This is a video',content_loc:"http://tintech.vn"+it}
      //    videos.push(vd);
      //  }
       var lM=new Date(item.createtime);
       if(item.lastmodified!="0000-00-00 00:00:00") lM=new Date(item.lastmodified);
       var lastM=lM.getFullYear() + "-" + ('0' + (lM.getMonth()+1)).slice(-2) + "-" + ('0' + lM.getDate()).slice(-2) + "T" + pad(lM.getHours()) + ":" + pad(lM.getMinutes()) + ":" + pad(lM.getSeconds())+"+07:00";
       console.log(lastM)
      // var obj={url: url,video:videos,img:imgs,lastmodISO:lastM,changefreq: 'weekly', priority: 0.8};
       var obj={url: url,img:imgs,lastmodISO:lastM,changefreq: 'weekly', priority: 0.8};
       urls.push(obj);
       imgs=[];
     }
     var sm = require('sitemap');
     content = sm.createSitemap({
        hostname: 'http://www.tintech.vn',
        //cacheTime: 600000,  //600 sec (10 min) cache purge period
        urls:urls
      }).toString();
   }
   this.header('Content-Type', 'application/xml');
   this.end(content);
  }
