    //分类
    async categoryAction(){
      let pagenumber=this.get("page")||1;
      let pagesize=this.get("pagesize")||10;
      let aurl=await this.get("aurl");
      let arrU=aurl.split('-');
      let itemId=arrU.splice(-1, 1);
      let urlrewrite=arrU.join("-");
        let category=await this.model("home").findOne("tags",{urlrewrite:urlrewrite,id:itemId});
        if(category.tagname) {
            let itemList = await this.model("home").getPageSelect({keywords: ["like", "%,"+itemId.toString()+",%"], ispublished: 1}, pagenumber, pagesize);
            let result = await this.model("home").getPageCountSelect({
                keywords: ["like", "%,"+itemId.toString()+",%"],
                ispublished: 1
            }, pagenumber, pagesize);
            let Page = think.adapter("template", "page");
            let page = new Page(this.http);
            let pageData = page.pagination(result);
            console.log("tag"+itemList);
            this.assign("itemList", itemList);
            this.assign('pageData', pageData);
            //分页
            this.assign('category', category);
            this.assign('more', 0);
            this.assign('menu', 'tag/'+urlrewrite+'-'+ itemId);
            return this.displayView('index_category');
        }else{
            return this.displayView("../common/error_404");
        }

    }

