    async removereplyAction(){
        let data=await this.post();
        let myid=data.id;
        if(!think.isEmpty(myid)){
            let rs=await this.model("article_comment").where({id:myid}).delete();
            // 减少回复数
            let replycount=await this.model("article").where({id:data.tid}).decrement('replycount',1);
            if(rs) return this.success();
        }
    }
    async postlikeAction(){
        let data=await this.post();
        let liker=data.likers;
        let myid=data.id;
        if(!think.isEmpty(myid)){
            let item=await this.model("home").findOne("article_comment",{id:myid});
            let arr=(!item.likers)?[]:(item.likers).split(",");
            let likers=arr||[];
            let n=likers.indexOf(liker);
            if(n<0){
              likers.push(liker);
              let newlikers=likers.join(",");
              let m=likers.length;
              let rs=await this.model("article_comment").where({id:myid}).update({like:m,likers:newlikers});
              if(rs) return this.success({likeCount:m});
            }else{
              likers.splice(n,1);
              let m=likers.length;
              let newlikers=likers.join(",");
              let rs=await this.model("article_comment").where({id:myid}).update({like:m,likers:newlikers});
              if(rs) return this.success({likeCount:m});
            }
        }
    }

